import { getMusicList } from "@/request/api/home";
export default {
  name: "musicList",
  data() {
    return {
      musicList: []
    };
  },
  methods: {
    async getGedan() {
      let res = await getMusicList();
      this.musicList = res.data.result;
    },
    changeCount: function (num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    }
  },
  mounted() {
    this.getGedan();
  }
};