import { likeMusic, getMusicDowload, getMusicComment } from "@/request/api/home.js";
import { Vue3Marquee } from "vue3-marquee";
import "vue3-marquee/dist/style.css";
import { mapMutations, mapState } from "vuex";
import { showFailToast } from "vant";
export default {
  name: "MusicDetail",
  props: ["musicList", "isbtnShow", "changeDetailShow", "play", "detailShow", "lyricList", "addDuration"],
  mounted() {
    this.addDuration();
    console.log(this.lyricList);
    // this.actions[0].text = this.playList[0].name;
  },

  components: {
    Vue3Marquee
  },
  data() {
    return {
      isLyricShow: false,
      showPopover: false,
      loading: true,
      actions: [],
      visible: false,
      showComment: false,
      musicComments: [],
      nowCurrentTime: null
    };
  },
  methods: {
    open4: function () {
      ElMessage.error("Oops, this is a error message.");
    },
    goPlay: function (num) {
      this.$refs.musicLyric.scrollTop = 0;
      let index = this.playListIndex + num;
      if (index == -1) {
        index = this.playList.length - 1;
      } else if (index == this.playList.length) {
        index = 0;
      }
      this.updatePlayListIndex(index);
    },
    handleClick() {
      this.$emit("update:currentTime", 0);
    },
    doLikeMusic: async function () {
      let result = await likeMusic(this.$store.state.playList[this.$store.state.playListIndex].id);
      if (result.status == 200) {
        document.getElementsByName("xihuan")[0].style.fill = "red";
      }
    },
    incrementPlayListIndex: function () {
      // let num = playList.length;
      this.updatePlayIndex(this.playList.length - 1);
      // this.updatePlayIndex(num + 1);
    },

    playMusic: function (i) {
      this.updatePlayListIndex(i);
    },
    dowloadMusic: async function (data) {
      let result = await getMusicDowload(this.playList[this.playListIndex].al.id);
      if (result.data.data.url === null) {
        showFailToast("无下载链接");
      }
      console.log(this.visible);
      console.log(result);
    },
    sleep: function (d) {
      for (var t = Date.now(); Date.now() - t <= d;);
    },
    getMusicComment: async function () {
      this.showComment = true;
      this.musicComments = (await getMusicComment(this.musicList.id)).data.comments;
      console.log(this.musicComments);
    },
    ...mapMutations(["updateAllPlayList", "updatePlayList", "updatePlayIndex", "updatePlayListIndex", "updateCurrentTime"])
  },
  watch: {
    currentTime: function (newValue) {
      let p = document.querySelector("p.active");
      if (p) {
        if (p.offsetTop > 300) {
          this.$refs.musicLyric.scrollTop = p.offsetTop - 270;
        }
      }
      if (newValue === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
        } else {
          this.updatePlayListIndex(this.playListIndex + 1);
        }
      }
    },
    nowCurrentTime: function (newValue) {
      this.updateCurrentTime(newValue);
    }
  },
  computed: {
    ...mapState(["playListIndex", "playList", "duration", "currentTime"]),
    lyric: function () {
      let arr;
      console.log(this.lyricList);
      if (this.lyricList.lrc.lyric) {
        arr = this.lyricList.lrc.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(item.indexOf(".") + 1, item.indexOf("]"));
          let lrc = item.slice(11, item.length);
          let time = parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          return {
            min,
            sec,
            mill,
            lrc,
            time
          };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            item.next = 10000;
          } else {
            item.next = arr[i + 1].time;
          }
        });
      }
      return arr;
    }
  }
};